
import PropTypes from 'prop-types';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import { useCallback, useEffect, useMemo, useState } from 'react';

// form
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import {
  Box, Typography, Grid, Card, Stack, Paper, Select, InputLabel,
  Button, OutlinedInput, MenuItem, Checkbox, ListItemText, FormControl
} from '@mui/material';
// hooks
import useAuth from '../../../../hooks/useAuth';
// 
// import {useDispatch,useSelector,category} from '../../../../redux/category';

// components
import { FormProvider, RHFUploadCategoryCoverFile, RHFSelect, RHFTextField } from '../../../../components/hook-form';

// ----------------------------------------------------------------------

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};



UserCategoryCreate.propTypes = {
  currentCategory: PropTypes.object,
  onCreate: PropTypes.func,
  isEdit: PropTypes.bool
};

export default function UserCategoryCreate({ isEdit = false, currentCategory, onCreate }) {
  const { user } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  // const {account, isLoading} = useSelector((state)=>state.account);
  const isLoading = false
  // const { position, cover } = currentUser;
  const mainCategories = ['sample1', 'sample2', 'sample3', 'sample4'];

  const NewUserSchema = Yup.object().shape({
    categoryName: Yup.string().required('Category is required'),
    categoryCoverUrl: Yup.string().required('Cover is required'),
    categoryParrents: Yup.array().required('Parrent Category is required'),
  });

  const defaultValues = useMemo(
    () => ({
      categoryName: currentCategory?.categoryName || 'Cergey Stroe',
      categoryAbout: currentCategory?.categoryAbout || 'about',
      categoryCoverUrl: currentCategory?.categoryCoverUrl || '',
      categoryParrents: currentCategory?.categoryParrents || ['sample1', 'sample3'],
    }),
    [currentCategory]
  );

  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    control,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    if (currentCategory) {
      reset(defaultValues);
    }

  }, [currentCategory]);

  const onSubmit = async () => {
    try {
      await new Promise((resolve) => setTimeout(resolve, 500));
      reset();
      onCreate();
      enqueueSnackbar(!isEdit ? 'Create success!' : 'Update success!');
    } catch (error) {
      console.error(error);
    }
  };

  const handleCoverDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];

      if (file) {
        setValue(
          'categoryCoverUrl',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
    },
    [setValue]
  );

  const handleSelectChange = useCallback(
    (event) => {
      const {
        target: { value },
      } = event;
      setValue('categoryParrents',
        // On autofill we get a stringified value.
        typeof value === 'string' ? value.split(',') : value,
      );
    }, [setValue]
  )



  return (
    <>

      <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
        <Card sx={{ mt: 5, py: 3, px: 1, textAlign: 'center' }}>
          <Grid container spacing={2}>
            <Grid item xs={12} md={7} >
              <Paper sx={{
                borderRadius: 1, overflow: 'hidden', border: '1px solid lightgrey', bgcolor: 'grey.200',
                '&:hover': { boxShadow: `0px 4px 10px 3px #919eabcc` }
              }} >
                <Box sx={{ position: 'relative', width: '100%', paddingTop: 'calc(1/3*100%)', overflow: 'hidden' }}>
                  <RHFUploadCategoryCoverFile
                    name="categoryCoverUrl" accept="image/*" maxSize={3145728} onDrop={handleCoverDrop}
                    sx={{ position: 'absolute', top: 0, left: 0, right: 0 }}
                  />
                </Box>

                <Typography sx={{
                  color: 'text.main', bgcolor: 'white', textAlign: 'center',
                  py: { xs: 1.5, sm: 2, lg: 2.5 }
                }} variant={"h5"} >
                  {values.categoryName}</Typography>
              </Paper >
            </Grid>

            <Grid item xs={12} md={5}>
              <Stack sx={{ height:'100%'}} gap={2} justifyContent={'space-between'}>
                <RHFTextField name="categoryName" label="Category Name" value={values.categoryName} />
                <FormControl >
                  <InputLabel id="parrentCategorySelect">Parrent Categories</InputLabel>
                  <Select
                    labelId="parrentCategorySelect"
                    name="categoryParrents"
                    multiple
                    value={values.categoryParrents}
                    onChange={handleSelectChange}
                    input={<OutlinedInput label="Parrent Categories" />}
                    renderValue={(selected) => selected.join(', ')}
                    MenuProps={MenuProps}
                    sx={{textAlign:'left'}}
                  >
                    {mainCategories.map((category) => (
                      <MenuItem key={category} value={category}>
                        <Checkbox checked={values.categoryParrents.indexOf(category) > -1} />
                        <ListItemText primary={category} />
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <RHFTextField name="categoryAbout" label="About This Category" value={values.categoryAbout} multiline rows={4} />
              </Stack>
            </Grid>
          </Grid>
          <Stack spacing={3} alignItems="flex-end" sx={{ mt: 3 }}>
            <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
              Create Category
            </LoadingButton>
          </Stack>
        </Card>
      </FormProvider >

    </>

  );
}



