import PropTypes from 'prop-types';
// @mui
import { useTheme } from '@mui/material/styles';
import { Paper, Typography ,Box} from '@mui/material';

// components 
import Image from '../../../../components/Image';

CategoryCardList.propTypes = {
  items: PropTypes.array,
  count: PropTypes.number,
};

export default function CategoryCardList({ items, count=0, ...other }) {
  const theme = useTheme();
  console.log(count);
  const fakes = [...Array(count)].map((_, index) => ({
    categoryName: 'newcategory',
    categoryCover: `/images/covers/cover_${index + 1}.jpg`,
  }));

  return (
    <Box sx={{
      display: 'grid', gap: 2,
      gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)', lg: 'repeat(3, 1fr)' },
    }}>

      {(fakes).map((item, index) => (
        <Paper key={index} sx={{
          my: 2, borderRadius: 1, bgcolor: 'background.neutral', overflow: 'hidden',
          border: '1px solid lightgrey',
          '&:hover': { boxShadow: `0px 4px 10px 3px ${theme.palette.grey[500_80]}`, cursor: 'pointer' }
        }} {...other}>

          <Image src={item.categoryCover} alt={item.categoryName} ratio="3/1" />

          <Typography sx={{ color: 'text.main', textAlign: 'center', py: 1.5 }} variant={"h5"} >{item.categoryName}</Typography>
        </Paper >
      ))}
    </Box>
  )
}