import { useEffect, useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
// @mui
import { Card, Container, Stack, Box, Button } from '@mui/material';
// redux
import { useDispatch } from '../../redux/store';
import { getConversations, getContacts } from '../../redux/slices/chat';
// routes
import { PATH_APPLICATION } from '../../routes/paths';
// hooks
import useAuth from '../../hooks/useAuth';
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { UserStoreCreate, UserCategoryCreate,CategoryCardList } from '../../sections/@dashboard/user/store';
// _mock
import { _userAbout, _userFeeds, _userFriends, _userGallery, _userFollowers } from '../../_mock';
// ----------------------------------------------------------------------
export default function UserCreateStore() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { currentUser } = useAuth();
  const navigate = useNavigate();
  const [openCate, setOpenCate] = useState(false);
  const [count,setCount] =useState(0);
   
  useEffect(() => {
    dispatch(getConversations());
    dispatch(getContacts());
  }, [dispatch]);

  return (
    <Page title="HHU|| User Store">
      <Container maxWidth={themeStretch ? false : 'xl'} >

        {/* <Stack justifyContent={'space-between'} direction={'row'} alignItems={'center'}> */}
        <HeaderBreadcrumbs
          heading="Create New Store"
          links={[
            { name: 'App', href: PATH_APPLICATION.root },
            { name: 'Profile', href: PATH_APPLICATION.profile.root },
            { name: `Store` },
          ]}
          sx={{ mb: 3 }}
        />

        <UserStoreCreate currentUser={currentUser} />
        <CategoryCardList count={count}/>
        {openCate
          ? <UserCategoryCreate currentUser={_userAbout} onCreate={() => {setCount(count+1)}} />
          : <Box textAlign={'center'} mb={3}>
            <Button variant="contained" sx={{ width: { xs: '70%', sm: '30%' } }} size="large"
              onClick={() => setOpenCate(true)}
            >
              Create Category
            </Button>
          </Box>
        }


      </Container>
    </Page>
  );
}







